/*  --------Wishlist App Bar-------- */

/*  --------Wishlist Drawer-------- */
.wishlist__drawer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

/* --------Wishlist Main - content-------- */

.wishlistMain__heading {
	position: relative;
	color: rgb(20 43 87);
	font-weight: 550;
	font-size: 3em;
	margin-bottom: 2rem !important;
	width: 100px;
	font-family: "Oswald";
}

.wishlistMain__heading:after {
	display: block;
	content: "";
	width: 10rem;
	height: 3px;
	background: #fa5051;
	position: absolute;
	bottom: 0;
	left: 0;
}
.wishlist__product {
	height: 100%;
	width: 250px;
	/* border: 2px solid; */
	box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
	padding: 10px 10px;
	margin: 15px 0px;
}

.wishlistProduct__image {
	height: 240px;
	width: 100%;
	padding: 10px;
	object-fit: contain;
	transform: scale(1, 1);
	transition: transform 0.8s ease;
}

#wrapper .quickViewModal__txt {
	text-align: center;
	font-size: 1rem;
	font-weight: 500;
	position: relative;
	left: 0px;
	visibility: hidden;
	cursor: pointer;
}

#wrapper:hover .quickViewModal__txt {
	background-color: rgb(20 43 87);
	color: #fff;
	visibility: visible;
}

.wishlistProduct__image:hover {
	transform: scale(1.2, 1.2);
	cursor: pointer;
	/* z-index: 9999; */
}

.wishlist__productContent {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 10px 0px;
	height: 100px;
	width: 90%;
	margin: auto;
}

.wishlistProduct__title {
	color: rgb(20 43 87);
	font-weight: 600;
	text-transform: uppercase;
	font-size: 0.95rem;
}

.wishlistProduct__price {
	color: #fa5051;
	font-weight: 750;
	font-size: 0.95rem;
}

.wishlist__addToCartBtn {
	background-color: rgb(20 43 87) !important;
	color: #fff;
}

.wishlist__productDetailBtn {
	margin-top: 5px !important;
	background-color: rgb(20 43 87) !important;
	color: #fff;
}

.wishlist__threeBtns {
	display: flex;
	justify-content: flex-end;
	margin-top: 1rem;
}

.wishlist__threeBtns > Button:nth-child(2) {
	margin: 0 5rem;
}

.WishlistBtns {
	font-family: "Bebas Neue" !important;
	background: #ff4f4f !important;
	color: #fff !important;
	padding: 12px 0 !important;
	font-size: 1.1rem !important;
	/* font-weight: 600 !important; */
	/* letter-spacing: 5px !important; */
}

/* --------Wishlist Modal--------  */

.wishlist__modal {
	display: flex;
	align-items: center;
	justify-content: center;
	backdrop-filter: blur(1px);
}

.wishlistModal__paper {
	border: 2px solid;
	padding: 15px;
	background-color: #fff;
}
